.SideDrawer {
  width: 60%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(darkblue, rgb(8, 185, 255), rgb(13, 104, 13));
  position: fixed;  
  top: 0%;
  left: 25%;
  z-index: 1000;
  transform: translateX(-45%);
  transition: transform 300ms ease-out;
}

.SideDrawer_Hidden {
  transform: translateX(-150%) !important;
}

.Link_List {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: none;
}

@media (max-width: 900px) {
  .Link_List {
    flex-direction: column;
    padding: 0;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
